// CSS
import sideMenu from '../../../assets/SideMenu.module.css';

// IMAGE
import close from '../../../assets/close.svg';

const Root = (props) => {

    let asideClassNames = `${sideMenu['drawer']}`;
    if(props.stateToggle){
        asideClassNames = `${sideMenu['drawer']} ${sideMenu['drawer--open']}`;
    }

    return (
        <aside className={asideClassNames}>
        <button className={`${sideMenu['drawer__btn']}`} onClick={props.onToggle}>
            <img className={`${sideMenu['drawer__btn__img']}`} src={close} alt="Close" />
        </button>
        <div className={`${sideMenu['drawer__logo']}`}>
            <h2 className={`${sideMenu['drawer__logo__h']}`}>{props.title}</h2>
        </div>
        <div className={`${sideMenu['drawer__desc']}`}>
            <p>{props.desc}</p>
            <p><a href="#">{props.intro}</a></p>
        </div>
        {props.children}
    </aside>
    );
};

export default Root;