// CSS
import docsClass from '../../../assets/Documentation.module.css';
import globalClass from '../../../assets/Global.module.css';

const Collapse = (props) => {
    let textContent = 'Collapse all cards';
    if(props.stateCollapse){
        textContent = 'Expand all cards';
    }

    return (
        <div className={docsClass['main-collapser']}>
        <button className={`${globalClass['btns']}`} onClick={props.onCollapseCards}>
            <span className={'collapser-content'}>{textContent}</span> <span className={globalClass['btns__ups']}>up</span>
        </button>
        </div>
    )
}

export default Collapse;