// CSS
import docsClass from '../../assets/Documentation.module.css';
import globalClass from '../../assets/Global.module.css';

// IMPORT
import AccordionRoot from './AccordionRoot/AccordionRoot';
import Collapse from './Collapse/Collapse';

const Documentation = (props) => {

    let mainClassNames = `${docsClass['main-content']} ${docsClass['main-content--hide']}`;
    if(props.stateToggle){
        mainClassNames = `${docsClass['main-content']}`;
    }

    return (
        <div className={mainClassNames}>                        
            <div>
                <Collapse 
                onCollapseCards={props.onCollapseCards}
                stateCollapse={props.stateCollapse} />
                <div className={`${globalClass['grids']} ${globalClass['grids--docs-grid']}`}>
                    <AccordionRoot 
                    stateDocs={props.stateDocs}
                    filteredStateDocs={props.filteredStateDocs}
                    onAccordionParent={props.onAccordionParent} 
                    onAccordionChild={props.onAccordionChild}/>
                </div>
            </div>
        </div>
    )
};

export default Documentation;