// CSS
import globalClass from '../../../../assets/Global.module.css';

// IMAGE
import check from '../../../../assets/check.svg';

const InputCheckbox = (props) => {

    const filters = props.filters.map(filter => {
        return <div className={globalClass['cheat-c']} key={filter.key}>
        <label className={globalClass['cheat-c__label']} htmlFor="basics">
            <div className={globalClass['cheat-c__label__c']}>
                <span className={globalClass['cheat-c__label__check']}><img className={globalClass['cheat-c__label__img']} src={check} alt="Check"/></span>
                <input className={`${globalClass['hidden']} ${globalClass['filter-me']}`} type="checkbox" name="basics" id="basics"/>
                <div className={globalClass['cheat-c__label__name-c']}>
                    <p className={globalClass['no-margin']}>{filter.title}</p>
                </div>
            </div>
        </label>
       </div>
    })

    return (
        filters
    );
};

export default InputCheckbox;