// Components
import InputText from './SideMenuInputs/InputText/InputText';
import InputCheckbox from './SideMenuInputs/InputCheckbox/InputCheckbox';
import Root from './Root/Root';

const SideMenu = (props) => {

    return (
        <Root 
        title={props.sideMenuData.title} 
        desc={props.sideMenuData.desc} 
        intro={props.sideMenuData.intro} 
        onToggle={props.onToggle} 
        stateToggle={props.stateToggle}>
            <InputText
            onSearch={props.onSearch}/>
            <InputCheckbox filters={props.sideMenuData.filters}/>
        </Root>

    )
};

export default SideMenu;