// IMPORT
import Accordion from './Accordion/Accordion';
import AccordionInner from './Accordion/Accordion-inner/Accordion-inner';

const AccordionRoot = (props) => {
  let mapState = props.stateDocs;
  
  if(props.filteredStateDocs.length > 0){
    mapState = props.filteredStateDocs;
  }

  const accordion = mapState.map( doc => {
    return (
      <div key={doc.id}>
      <Accordion 
      accordTitle={doc.accordTitle}
      accordDesc={doc.accordDesc}
      onAccordionParent={props.onAccordionParent}
      id={doc.id}
      show={doc.show}
      accordLink={doc.links}
      >
        {doc.accordChild.map( childDoc => {
          return <AccordionInner 
          key={childDoc.id} 
          parentId={doc.id} 
          show={childDoc.show} 
          id={childDoc.id} 
          title={childDoc.accordTitle} 
          desc={childDoc.accordDesc}
          links={childDoc.links}
          onAccordionChild={props.onAccordionChild} />
        })}
      </Accordion>
      </div>
    )
  })
    return accordion;
};

export default AccordionRoot;