// IMAGE
import search from '../../../../assets/search.svg';

// CSS
import globalClass from '../../../../assets/Global.module.css';

const InputText = (props) => {
    return (
        <form className={globalClass['cheat-c']} onSubmit={props.onSearch}>
        <button className={globalClass['cheat-c__btn']}><img className={globalClass['cheat-c__btn__srch']} src={search} alt="Search"/></button>
        <input className={globalClass['cheat-c__input']} type="text" placeholder="Enter your search" />
        </form>
    )
};

export default InputText;