// IMPORT
import docsClass from '../../../../../assets/Documentation.module.css';

// IMAGES
import arrowUp from '../../../../../assets/arrow.png';

const AccordionInner = (props) => {

    let accordionClass= `${docsClass['accordion-child']} ${docsClass['accordion-child--hide']}`;
    if(props.show){
        accordionClass = `${docsClass['accordion-child']}`;
    }

    let links;
    if(props.links !== undefined && props.links.length > 0){
        links = props.links.map(item => {
            let tags;
            if(item.tags !== undefined){
                tags = item.tags.map( tag => {
                    return <span>{tag}</span>
                });
            }
            return (
            <div className={docsClass['links-parent__item']}>
                <p className={docsClass['links-parent__link-title']}>{item.title}</p>
                <p className={docsClass['links-parent__link-note']}>{item.note}</p>
                <p className={docsClass['links-parent__time-stamp']}>{item.timestamp}</p>
                <a target="_blank" rel="noreferrer" href={item.link} className={docsClass['links-parent__link']}>Link</a>
                <div>{tags}</div>
            </div>
            );
        })
    }

    let accordionTitle;
    accordionTitle = <span className={`accord-title-${props.id}`} dangerouslySetInnerHTML={{__html: props.title}}/>

    return (
        <div className={accordionClass}>
        <div className={docsClass['accordion-child__header']} onClick={props.onAccordionChild.bind(this, props.parentId, props.id)}> 
            {accordionTitle}
            <img className={docsClass['accordion-child__header__img']} src={arrowUp} alt="Arrow up" />
        </div>
        <div className={docsClass['accordion-child__content']}>
            <div className={docsClass['accordion-child__content__item']}>
                {props.desc}
                <div className={docsClass['links-parent']}>
                  {links}
                </div>
            </div>
        </div>
    </div>
    )
};

export default AccordionInner;