import Root from './containers/root/Root';
import SideMenu from './components/SideMenu/SideMenu';
import Documentation from './components/Documentation/Documentation';

// CSS
import global from './assets/Global.module.css';

// STATIC DATA
import sideMenuJSON from './JSON/sideMenu';
import documentationJSON from './JSON/documentation';

// REACT MODULE
import { useState, useEffect, useCallback } from 'react';

document.body.classList.add(`${global['body']}`);
const App = () => {

    // STATE
    const [stateSideMenu] = useState(sideMenuJSON);
    const [stateDocs, setStateDocs] = useState(documentationJSON);
    const [stateSideMenuToggle, setStateSideMenuToggle] = useState(true);
    const [stateCollapse, setStateCollapse] = useState(false);
    const [filteredStateDocs, setFilteredStateDocs] = useState([]);
    const onSideMenuToggle = () => {
        setStateSideMenuToggle(prevState => {
            return !prevState;
        });
    };

    // const fetchDocs = useCallback( async () => {
    //     const response = await fetch('https://react-js-docs-default-rtdb.firebaseio.com/docs.json', {
    //         method:'GET',
    //         headers:{
    //             'Content-Type':'application/json'
    //         }
    //     });
    //     const data = await response.json();
    //     setStateDocs(data[Object.keys(data)[0]]);
    // }, []);
    

    // useEffect(() => {
    //     fetchDocs();
    // },[fetchDocs]);

    const onAccordion = (id) => {
        if(filteredStateDocs.length > 0){
            let copyState = JSON.parse(JSON.stringify(filteredStateDocs));
            let getCurrentIndex = copyState.findIndex( doc => doc.id === id);
            copyState[getCurrentIndex].show = !copyState[getCurrentIndex].show;
            setFilteredStateDocs(copyState);
        }
        else{
            let copyState = JSON.parse(JSON.stringify(stateDocs));
            let getCurrentIndex = copyState.findIndex( doc => doc.id === id);
            copyState[getCurrentIndex].show = !copyState[getCurrentIndex].show;
            setStateDocs(copyState);
        }
    }

    const onAccordionChild = (parentId, id) => {
            if(filteredStateDocs.length > 0){
                let copyState = JSON.parse(JSON.stringify(filteredStateDocs));
                let getParentIndex = copyState.findIndex(doc => doc.id === parentId);
                let getParentDoc = copyState.find(doc => doc.id === parentId);
                let getChildDocIndex = getParentDoc.accordChild.findIndex( doc => doc.id === id);
                copyState[getParentIndex].accordChild[getChildDocIndex].show = !copyState[getParentIndex].accordChild[getChildDocIndex].show;
                setFilteredStateDocs(copyState);
            }else{
                let copyState = [...stateDocs];
                let getParentIndex = copyState.findIndex(doc => doc.id === parentId);
                let getParentDoc = copyState.find(doc => doc.id === parentId);
                let getChildDocIndex = getParentDoc.accordChild.findIndex( doc => doc.id === id);
                copyState[getParentIndex].accordChild[getChildDocIndex].show = !copyState[getParentIndex].accordChild[getChildDocIndex].show;
                setStateDocs(copyState);
            }
    }

    const onCollapseCards = () => {
        let copyState = [...stateDocs];
        if(stateCollapse){
            copyState.map(doc => doc.show = true);
        }else{
            copyState.map(doc => doc.show = false);
        }
        setStateCollapse(prevState => !prevState);
        setStateDocs(copyState);
    }

    const onSearch = (e) => {
        e.preventDefault();
        let input = e.target.querySelector('input');
        let inputValue = input.value.trim();
        let copyState = JSON.parse(JSON.stringify(stateDocs));
        let searchItem = new RegExp(inputValue,"i");
        let searchResult;
        if(inputValue !== '' && inputValue !== undefined){
            searchResult = copyState.filter(doc => {

               let childSearchTrue = false;

               doc.accordChild.map(child => {
                    if(child.accordTitle.toLowerCase().includes(inputValue.toLowerCase())){
                        childSearchTrue = true;
                        child.accordTitle = child.accordTitle.replace(searchItem, `<mark>${inputValue}</mark>`);
                        return child;
                    }
                    return '';
               });

               if(doc.accordTitle.toLowerCase().includes(inputValue.toLowerCase()) || childSearchTrue){
                doc.accordTitle = doc.accordTitle.replace(searchItem, `<mark>${inputValue}</mark>`);
                return doc;
               }

               return '';
            });
        }
        else{
            searchResult = copyState;
        }
        setFilteredStateDocs(searchResult);
    }

    return (
        <Root onToggle={onSideMenuToggle}>
            <SideMenu 
            sideMenuData={stateSideMenu} 
            onToggle={onSideMenuToggle} 
            stateToggle={stateSideMenuToggle}
            onSearch={onSearch}/>
            <Documentation 
            stateDocs={stateDocs}
            filteredStateDocs={filteredStateDocs}
            stateToggle={stateSideMenuToggle} 
            onAccordionParent={onAccordion} 
            onAccordionChild={onAccordionChild}
            onCollapseCards={onCollapseCards}
            stateCollapse={stateCollapse} />
        </Root>
    )
}

export default App;