import docsClass from '../../../../assets/Documentation.module.css';

// IMAGES
import arrowUp from '../../../../assets/arrow.png';

import { Fragment } from 'react';

const Accordion = (props) => {
    
    let accordionClass = `${docsClass['accordion']} ${docsClass['accordion--hide']}`;
    if(props.show){
        accordionClass = `${docsClass['accordion']}`;
    }

    let accordionLinks;
    if(props.accordLink.length > 0){
        accordionLinks = props.accordLink.map( link => {
            return <Fragment>
                <p>{link.title }</p>
                <a href="link.link">Link</a>
            </Fragment>
        })
    }

    let accordionTitle;
    accordionTitle = <span className={`accord-title-${props.id}`} dangerouslySetInnerHTML={{__html: props.accordTitle}}/>

    return (
        <div className={accordionClass}>
        <div className={docsClass['accordion__header']} onClick={() => props.onAccordionParent(props.id)}> 
            {accordionTitle}
            <img className={docsClass['accordion__header__img']} src={arrowUp} alt="Arrow up" />
        </div>
        <div className={docsClass['accordion__content']}>
            <div className={docsClass['accordion__content__item']}>
                {props.accordDesc}
                {accordionLinks}
            </div>
            {props.children}
        </div>
    </div>
    )
}

export default Accordion;